import Vue from 'vue/dist/vue.js'
import VueRouter from 'vue-router'
import vueResource from 'vue-resource'

import Register from './components/Register.vue'
import create_order from './components/create_order.vue'
import orders from './components/orders.vue'
import OrderDetails from './components/OrderDetails'
import SignIn from './components/SignIn'
import prof from './components/prof'

const baseURL = 'http://127.0.0.1:8100';

Vue.use(vueResource)
Vue.use(VueRouter)

const router = new VueRouter({
    mode: 'history',
    base: __dirname,
    routes : [
        {path: '/register', component: Register},
        {path: '/login', component: SignIn},
        {path: '/profile', component: prof},
        {path: '/', component: create_order},
        {path: '/orders/add', component: create_order},
        {path: '/orders', component: orders},
        {path: '/order/:id', component: OrderDetails}
    ]
})


new Vue({
  router,
    template:`
    <div id="app">
        <router-view></router-view>
    </div>`
}).$mount('#app')
