<script>
    const baseURL = 'http://127.0.0.1:8100';
    export default {
        name: "Register",
        data (){
            return {
                user: {},
                is_valid: false
            }
        },
        methods: {
            addUser(e){
                let newUser = this.user;
                this.$http.post(`${baseURL}/rest-auth/registration/`, newUser);
                e.preventDefault()
            },
            validateEmail(email) {
                let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                return re.test(email);
            },

            check_passwords_match() {
                if (this.user.password1.length > 5 && this.user.password2.length > 5) {
                    if (this.user.password2 !== this.user.password1) {
                        this.pwd2_msg = 'Passwords do not match';
                        this.disable_btn = true;
                        return true;
                    } else {
                        this.pwd2_msg = '';
                        this.disable_btn = false;
                        return false;
                    }
                }
            }

            }
        }
</script>

